import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import './website.css';
// import { unregister } from './registerServiceWorker';
// import * as serviceWorker from './serviceWorker';
import listUser from './components/user/listUser';
import createUser from './components/user/create/createUser';
import updateUser from './components/user/update/updateUser';
import Login from './components/login/login';
import createPost from './components/post/createPost';
import listPost from './components/post/post';
import updatePost from './components/post/updatePost';
import myProfile from './components/myprofile/myProfile';
import detailPost from './components/post/detailpost';
import { Provider } from 'react-redux';
import store from './config/redux/store';


ReactDOM.render(
	<Provider store={store}>
		<Router>
			<Switch>
				<Route path='/listuser' component={listUser} />
				<Route path='/create' component={createUser} />
				<Route path='/update/:id' component={updateUser} />
				<Route exact path='/' component={listPost} />
				<Route path='/createpost' component={createPost} />
				<Route path='/updatepost/:id' component={updatePost} />
				<Route path='/detailpost/:id' component={detailPost} />
				<Route path='/myprofile/:id' component={myProfile} />
				<Route path='/login' component={Login} />
			</Switch>
		</Router>
	</Provider>,
	document.getElementById('root')
);

